.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-main {
  padding: 20px;
}

.scores-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.score-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  margin: 10px 0;
  padding: 10px;
  border-radius: 8px;
  width: 80%;
  max-width: 600px;
}

.time {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 5px;
}

.teams {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.team {
  font-size: 1.5em;
  font-weight: bold;
}

.score {
  font-size: 1.5em;
  font-weight: bold;
  color: #ff0000;
  margin: 0 5px;
}

.colon {
  font-size: 1.5em;
  font-weight: bold;
  margin: 0 5px;
}

.App-footer {
  background-color: #282c34;
  padding: 10px;
  color: white;
  position: fixed;
  width: 100%;
  bottom: 0;
}